import { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import GraphVisualization from '../components/visualization/GraphVisualization.jsx';
import ChatInterface from '../components/chat/ChatInterface.jsx';
import DocumentUpload from '../components/DocumentUpload.jsx';
import {
  Network,
  MessageSquare,
  Upload,
  FileText,
  BarChart3,
  Settings,
  ChevronDown,
  ChevronUp,
  AlertCircle,
  Eye,
  X
} from 'lucide-react';

const UnifiedDashboard = () => {
  const [activeTab, setActiveTab] = useState('graph');
  const [graphData, setGraphData] = useState({ nodes: [], edges: [] });
  const [loading, setLoading] = useState(true);
  const [_error, setError] = useState(null);
  const [selectedNode, setSelectedNode] = useState(null);
  const [selectedResult, setSelectedResult] = useState(null);
  const [searchResults, setSearchResults] = useState([]);
  const [serviceStatus, setServiceStatus] = useState(null);
  const [documents, setDocuments] = useState([]);
  const [documentsLoading, setDocumentsLoading] = useState(false);
  const [errorModal, setErrorModal] = useState(null);
  const [chatCollapsed, setChatCollapsed] = useState(false);

  // Fetch initial graph data
  useEffect(() => {
    fetchGraphData();
    fetchDocuments();
    checkServiceStatus();
  }, []);

  const fetchGraphData = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/graph');
      setGraphData(response.data);
    } catch (err) {
      setError('Failed to fetch graph data. Please ensure the backend is running.');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const fetchDocuments = async () => {
    try {
      setDocumentsLoading(true);
      const response = await axios.get('/api/unstructured/documents/status');
      setDocuments(response.data.documents || []);
    } catch (err) {
      console.error('Failed to fetch documents:', err);
      // Fallback to basic documents endpoint
      try {
        const response = await axios.get('/api/unstructured/documents');
        setDocuments(response.data.documents || []);
      } catch (fallbackErr) {
        console.error('Fallback fetch also failed:', fallbackErr);
        setDocuments([]);
      }
    } finally {
      setDocumentsLoading(false);
    }
  };

  const checkServiceStatus = async () => {
    try {
      const response = await fetch('/api/unstructured/status');
      const data = await response.json();
      setServiceStatus(data.services);
    } catch (error) {
      console.error('Failed to check service status:', error);
    }
  };

  const handleGraphUpdate = useCallback((newData) => {
    setGraphData(newData);
  }, []);

  const handleNodeSelect = useCallback((nodeData) => {
    setSelectedNode(nodeData);
  }, []);

  const handleSearchResults = useCallback((results) => {
    setSearchResults(results);
    setSelectedResult(results.length > 0 ? results[0] : null); // Select first result by default
  }, []);

  const tabs = [
    { id: 'graph', label: 'Knowledge Graph', icon: Network },
    { id: 'documents', label: 'Document Library', icon: FileText },
    { id: 'upload', label: 'Upload Documents', icon: Upload },
    { id: 'analytics', label: 'System Analytics', icon: BarChart3 },
  ];

  const renderTabContent = () => {
    switch (activeTab) {
      case 'graph':
        return (
          <div className="w-full h-full flex flex-col">
            <div className="mb-4 flex justify-between items-center">
              <h2 className="m-0 text-gray-800 text-xl font-semibold">
                Knowledge Graph
              </h2>
              {selectedNode && (
                <div className="px-3 py-2 bg-blue-100 border border-blue-400 rounded-md text-xs text-blue-700">
                  Selected: {selectedNode.label}
                </div>
              )}
            </div>
            {/* Graph Visualization - Top Half */}
            <div className="flex-1 mb-4">
              <GraphVisualization
                graphData={graphData}
                onNodeSelect={handleNodeSelect}
                loading={loading}
              />
            </div>
            {/* Data Table - Bottom Half */}
            <div className="h-1/2 border-t border-gray-200 pt-4">
              <h3 className="text-lg font-medium text-gray-800 mb-3">
                {searchResults.length > 0 ? 'Search Results' : 'Graph Data Table'}
              </h3>
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
                <div className="overflow-x-auto max-h-64">
                  {searchResults.length > 0 ? (
                    // Search Results Table
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50">
                        <tr>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Type
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Source
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Content
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Score
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {searchResults.map((result, index) => (
                          <tr 
                            key={index} 
                            className={`hover:bg-gray-50 cursor-pointer ${selectedResult && selectedResult === result ? 'bg-blue-50' : ''}`}
                            onClick={() => setSelectedResult(result)}
                          >
                            <td className="px-4 py-3 whitespace-nowrap">
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                                {result.type || 'Result'}
                              </span>
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {result.source || 'Unknown'}
                            </td>
                            <td className="px-4 py-3 text-sm text-gray-900 max-w-xs truncate">
                              {result.content || result.text || result.label || 'No content'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {result.score || result.similarity_score || 'N/A'}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  ) : (
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50">
                        <tr>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Type
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Label
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Group
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Properties
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {graphData.nodes && graphData.nodes.map((node, index) => (
                          <tr
                            key={node.id || index}
                            className={`hover:bg-gray-50 cursor-pointer ${selectedNode && selectedNode.id === node.id ? 'bg-blue-50' : ''}`}
                            onClick={() => handleNodeSelect(node)}
                          >
                            <td className="px-4 py-3 whitespace-nowrap">
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                                Node
                              </span>
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                              {node.label || node.properties?.name || `Node ${node.id}`}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {node.group || 'Unknown'}
                            </td>
                            <td className="px-4 py-3 text-sm text-gray-500 max-w-xs">
                              <div className="space-y-1">
                                {node.properties ? Object.entries(node.properties).slice(0, 3).map(([key, value]) => (
                                  <div key={key} className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">{key}:</span>
                                    <span className="truncate text-right">{String(value).length > 20 ? `${String(value).substring(0, 20)}...` : String(value)}</span>
                                  </div>
                                )) : 'No properties'}
                                {node.properties && Object.keys(node.properties).length > 3 && (
                                  <div className="text-xs text-gray-400">+{Object.keys(node.properties).length - 3} more</div>
                                )}
                              </div>
                            </td>
                          </tr>
                        ))}
                        {graphData.edges && graphData.edges.map((edge, index) => (
                          <tr key={`edge-${index}`} className="hover:bg-gray-50">
                            <td className="px-4 py-3 whitespace-nowrap">
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                Edge
                              </span>
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                              {edge.label || 'Relationship'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {edge.from} → {edge.to}
                            </td>
                            <td className="px-4 py-3 text-sm text-gray-500 max-w-xs">
                              <div className="space-y-1">
                                {edge.properties ? Object.entries(edge.properties).slice(0, 3).map(([key, value]) => (
                                  <div key={key} className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">{key}:</span>
                                    <span className="truncate text-right">{String(value).length > 20 ? `${String(value).substring(0, 20)}...` : String(value)}</span>
                                  </div>
                                )) : 'No properties'}
                                {edge.properties && Object.keys(edge.properties).length > 3 && (
                                  <div className="text-xs text-gray-400">+{Object.keys(edge.properties).length - 3} more</div>
                                )}
                              </div>
                            </td>
                          </tr>
                        ))}
                        {(!graphData.nodes || graphData.nodes.length === 0) && (!graphData.edges || graphData.edges.length === 0) && (
                          <tr>
                            <td colSpan="4" className="px-4 py-8 text-center text-gray-500">
                              No graph data available. Upload documents or use the chat to build your knowledge graph.
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  )}
                </div>
              </div>
            </div>
          </div>
        );

      case 'documents':
        return (
          <div className="w-full h-full flex flex-col">
            {/* Header Section */}
            <div className="mb-6">
              <h2 className="text-2xl font-bold text-gray-800 mb-4">Document Library</h2>
              <p className="text-gray-600 mb-6">
                View and manage your processed documents. Monitor processing status and access detailed information.
              </p>

              {/* Service Status Overview */}
              {serviceStatus && (
                <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-4 mb-6">
                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Total Documents</p>
                        <p className="text-lg font-semibold text-blue-600">
                          {documents.length}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-blue-100">
                        <FileText className="w-3 h-3 text-blue-500" />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Successfully Processed</p>
                        <p className="text-lg font-semibold text-green-600">
                          {documents.filter(doc => doc.processing_status === 'completed').length}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-green-100">
                        <div className="w-3 h-3 rounded-full bg-green-500" />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Failed Processing</p>
                        <p className="text-lg font-semibold text-red-600">
                          {documents.filter(doc => doc.processing_status === 'failed').length}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-red-100">
                        <div className="w-3 h-3 rounded-full bg-red-500" />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Processing</p>
                        <p className="text-lg font-semibold text-yellow-600">
                          {documents.filter(doc => doc.processing_status !== 'completed' && doc.processing_status !== 'failed').length}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-yellow-100">
                        <div className="w-3 h-3 rounded-full bg-yellow-500 animate-pulse" />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Last Updated</p>
                        <p className="text-sm font-semibold text-gray-600">
                          {new Date().toLocaleTimeString()}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-gray-100">
                        <Settings className="w-3 h-3 text-gray-500" />
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Documents Table */}
              <div className="flex-1 flex flex-col bg-white rounded-lg shadow-md overflow-hidden">
                <div className="flex items-center justify-between p-4 border-b border-gray-200">
                  <h3 className="text-lg font-semibold text-gray-800">Document Management</h3>
                  <div className="flex items-center gap-3">
                    <button
                      onClick={fetchDocuments}
                      className="px-3 py-1 text-sm bg-blue-100 text-blue-700 rounded-md hover:bg-blue-200 transition-colors"
                      disabled={documentsLoading}
                    >
                      {documentsLoading ? 'Loading...' : 'Refresh'}
                    </button>
                    <button
                      onClick={() => setActiveTab('upload')}
                      className="px-3 py-1 text-sm bg-green-100 text-green-700 rounded-md hover:bg-green-200 transition-colors"
                    >
                      Upload Documents
                    </button>
                  </div>
                </div>
                <div className="flex-1 overflow-hidden">
                  <div className="overflow-x-auto h-full">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50 sticky top-0">
                        <tr>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Status
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Filename
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Type
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Size
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Processed At
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Logs
                          </th>
                          <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Properties
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {documents.map((doc, index) => (
                          <tr
                            key={`${doc.filename || 'unknown'}-${index}`}
                            className="hover:bg-gray-50"
                          >
                            <td className="px-4 py-3 whitespace-nowrap">
                              <div className="flex flex-col gap-1">
                                <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                  doc.processing_status === 'completed'
                                    ? doc.processing_stages && doc.processing_stages.length >= 4
                                      ? 'bg-green-100 text-green-800'
                                      : 'bg-yellow-100 text-yellow-800'
                                    : doc.processing_status === 'failed'
                                    ? 'bg-red-100 text-red-800'
                                    : 'bg-blue-100 text-blue-800'
                                }`}>
                                  {doc.processing_status === 'completed'
                                    ? doc.processing_stages && doc.processing_stages.length >= 4
                                      ? 'Fully Processed'
                                      : 'Partially Processed'
                                    : doc.processing_status === 'failed'
                                    ? 'Failed'
                                    : 'Processing'
                                  }
                                </span>
                                {doc.processing_stages && doc.processing_stages.length > 0 && (
                                  <div className="flex flex-wrap gap-1">
                                    {doc.processing_stages.slice(0, 3).map(stage => (
                                      <span key={stage} className="inline-flex items-center px-1.5 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                        {stage}
                                      </span>
                                    ))}
                                    {doc.processing_stages.length > 3 && (
                                      <span className="text-xs text-gray-500">+{doc.processing_stages.length - 3}</span>
                                    )}
                                  </div>
                                )}
                              </div>
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm font-medium text-gray-900">
                              {doc.original_filename || doc.filename || 'Unknown'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {doc.processing_type || 'Unknown'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {doc.file_size ? `${(doc.file_size / 1024).toFixed(1)} KB` : 'N/A'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              {doc.processed_at ? new Date(doc.processed_at).toLocaleDateString() : 'N/A'}
                            </td>
                            <td className="px-4 py-3 whitespace-nowrap text-sm text-gray-500">
                              <div className="flex flex-col gap-1">
                                {doc.processing_status === 'failed' && (
                                  <button
                                    onClick={() => setErrorModal(doc)}
                                    className="inline-flex items-center px-2 py-1 text-xs bg-red-50 text-red-700 rounded hover:bg-red-100 transition-colors"
                                  >
                                    <Eye className="h-3 w-3 mr-1" />
                                    View Error
                                  </button>
                                )}
                                {doc.processing_logs && doc.processing_logs.length > 0 && (
                                  <button
                                    onClick={() => setErrorModal(doc)}
                                    className="inline-flex items-center px-2 py-1 text-xs bg-blue-50 text-blue-700 rounded hover:bg-blue-100 transition-colors"
                                  >
                                    <FileText className="h-3 w-3 mr-1" />
                                    View Logs
                                  </button>
                                )}
                                {doc.processing_status === 'completed' && (
                                  <span className="inline-flex items-center px-2 py-1 text-xs bg-green-50 text-green-700 rounded">
                                    ✓ Success
                                  </span>
                                )}
                              </div>
                            </td>
                            <td className="px-4 py-3 text-sm text-gray-500 max-w-xs">
                              <div className="space-y-1">
                                {doc.has_content && (
                                  <div className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">Content:</span>
                                    <span className="truncate text-right">{doc.content_length} chars</span>
                                  </div>
                                )}
                                {doc.chunks_count > 0 && (
                                  <div className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">Chunks:</span>
                                    <span className="truncate text-right">{doc.chunks_count}</span>
                                  </div>
                                )}
                                {doc.embeddings_count > 0 && (
                                  <div className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">Embeddings:</span>
                                    <span className="truncate text-right">{doc.embeddings_count}</span>
                                  </div>
                                )}
                                {doc.has_vector_store && (
                                  <div className="flex justify-between text-xs">
                                    <span className="font-medium truncate mr-2">Vector Store:</span>
                                    <span className="truncate text-right text-green-600">✓</span>
                                  </div>
                                )}
                                {!doc.has_content && doc.chunks_count === 0 && doc.embeddings_count === 0 && !doc.has_vector_store && (
                                  <div className="text-xs text-gray-400 italic">
                                    No processing data available
                                  </div>
                                )}
                              </div>
                            </td>
                          </tr>
                        ))}
                        {documents.length === 0 && !documentsLoading && (
                          <tr>
                            <td colSpan="7" className="px-4 py-8 text-center text-gray-500">
                              <div className="space-y-3">
                                <FileText className="h-12 w-12 mx-auto text-gray-400" />
                                <div>
                                  <p className="text-lg font-medium">No documents found</p>
                                  <p className="text-sm">Upload documents to get started with your knowledge graph.</p>
                                  <button
                                    onClick={() => setActiveTab('upload')}
                                    className="mt-3 px-4 py-2 bg-blue-600 text-white text-sm rounded-md hover:bg-blue-700 transition-colors"
                                  >
                                    Upload Documents
                                  </button>
                                </div>
                              </div>
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );

      case 'upload':
        return (
          <div className="w-full h-full flex flex-col overflow-hidden">
            {/* Compact Header */}
            <div className="flex-shrink-0 px-6 py-4 border-b bg-white">
              <div className="flex items-center justify-between mb-4">
                <div>
                  <h2 className="text-2xl font-bold text-gray-800">Upload Documents</h2>
                  <p className="text-sm text-gray-600 mt-1">
                    Drag and drop files or click to browse. Multiple files supported.
                  </p>
                </div>
                <button
                  onClick={() => setActiveTab('documents')}
                  className="px-4 py-2 text-sm bg-gray-100 text-gray-700 rounded-md hover:bg-gray-200 transition-colors"
                >
                  View Library
                </button>
              </div>

              {/* Compact Stats and Legend */}
              <div className="flex items-center justify-between">
                {/* Stats */}
                <div className="flex items-center gap-6">
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-blue-500"></div>
                    <span className="text-sm font-medium text-gray-700">{documents.length} Total</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-green-500"></div>
                    <span className="text-sm font-medium text-gray-700">{documents.filter(doc => doc.processing_status === 'completed').length} Processed</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                    <span className="text-sm font-medium text-gray-700">{documents.filter(doc => doc.processing_status !== 'completed' && doc.processing_status !== 'failed').length} Processing</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-red-500"></div>
                    <span className="text-sm font-medium text-gray-700">{documents.filter(doc => doc.processing_status === 'failed').length} Failed</span>
                  </div>
                </div>

                {/* Compact File Types Legend */}
                <div className="flex items-center gap-4 px-4 py-2 bg-gray-50 rounded-lg">
                  <span className="text-xs font-medium text-gray-600">Supported:</span>
                  <span className="text-xs text-blue-600">PDF, DOCX, TXT</span>
                  <span className="text-xs text-gray-400">|</span>
                  <span className="text-xs text-green-600">PNG, JPG, TIFF</span>
                  <span className="text-xs text-gray-400">|</span>
                  <span className="text-xs text-purple-600">XLSX, CSV</span>
                </div>
              </div>
            </div>

            {/* Main Content Area - Split View */}
            <div className="flex-1 flex gap-6 p-6 overflow-hidden">
              {/* Upload Area - Left Side */}
              <div className="w-1/2 flex flex-col gap-4">
                {/* Drag & Drop Upload Zone */}
                <div className="bg-white rounded-lg shadow-md p-6 border-2 border-dashed border-gray-300 hover:border-blue-400 transition-colors">
                  <DocumentUpload
                    onUploadComplete={(result) => {
                      console.log('Upload completed:', result);
                      fetchDocuments();
                    }}
                  />
                </div>

                {/* Upload Instructions */}
                <div className="bg-blue-50 rounded-lg p-4">
                  <h4 className="text-sm font-semibold text-blue-900 mb-2">Quick Tips</h4>
                  <ul className="text-xs text-blue-700 space-y-1">
                    <li>• Drag and drop multiple files at once</li>
                    <li>• Files are processed automatically after upload</li>
                    <li>• Processing time varies based on file size and complexity</li>
                    <li>• Check the queue for real-time status updates</li>
                  </ul>
                </div>
              </div>

              {/* Processing Queue - Right Side */}
              <div className="w-1/2 flex flex-col bg-white rounded-lg shadow-md overflow-hidden">
                <div className="flex items-center justify-between p-4 border-b bg-gray-50">
                  <h3 className="text-lg font-semibold text-gray-800">Processing Queue</h3>
                  <button
                    onClick={fetchDocuments}
                    className="px-3 py-1 text-xs bg-blue-100 text-blue-700 rounded-md hover:bg-blue-200 transition-colors"
                    disabled={documentsLoading}
                  >
                    {documentsLoading ? 'Refreshing...' : 'Refresh'}
                  </button>
                </div>

                <div className="flex-1 overflow-y-auto">
                  {documents.length === 0 ? (
                    <div className="flex flex-col items-center justify-center h-full text-gray-400 p-8">
                      <Upload className="h-16 w-16 mb-4" />
                      <p className="text-lg font-medium">No documents in queue</p>
                      <p className="text-sm">Upload documents to see them here</p>
                    </div>
                  ) : (
                    <div className="divide-y divide-gray-200">
                      {documents.map((doc, index) => (
                        <div key={index} className="p-4 hover:bg-gray-50 transition-colors">
                          <div className="flex items-start gap-3">
                            {/* Status Icon */}
                            <div className={`flex-shrink-0 w-10 h-10 rounded-full flex items-center justify-center ${
                              doc.processing_status === 'completed' ? 'bg-green-100' :
                              doc.processing_status === 'failed' ? 'bg-red-100' : 'bg-yellow-100'
                            }`}>
                              {doc.processing_status === 'completed' ? (
                                <span className="text-green-600 text-lg">✓</span>
                              ) : doc.processing_status === 'failed' ? (
                                <AlertCircle className="h-5 w-5 text-red-600" />
                              ) : (
                                <div className="w-5 h-5 border-2 border-yellow-600 border-t-transparent rounded-full animate-spin"></div>
                              )}
                            </div>

                            {/* Document Info */}
                            <div className="flex-1 min-w-0">
                              <div className="flex items-center justify-between mb-1">
                                <p className="text-sm font-medium text-gray-900 truncate">
                                  {doc.original_filename || doc.filename || 'Unknown'}
                                </p>
                                <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${
                                  doc.processing_status === 'completed'
                                    ? 'bg-green-100 text-green-800'
                                    : doc.processing_status === 'failed'
                                    ? 'bg-red-100 text-red-800'
                                    : 'bg-yellow-100 text-yellow-800'
                                }`}>
                                  {doc.processing_status === 'completed'
                                    ? 'Completed'
                                    : doc.processing_status === 'failed'
                                    ? 'Failed'
                                    : 'Processing'
                                  }
                                </span>
                              </div>

                              {/* Processing Details */}
                              <div className="flex items-center gap-3 text-xs text-gray-500 mb-2">
                                <span>{doc.file_size ? `${(doc.file_size / 1024).toFixed(1)} KB` : 'N/A'}</span>
                                <span>•</span>
                                <span>{doc.processing_type || 'Unknown type'}</span>
                                <span>•</span>
                                <span>{doc.processed_at ? new Date(doc.processed_at).toLocaleDateString() : 'Processing...'}</span>
                              </div>

                              {/* Processing Stages */}
                              {doc.processing_stages && doc.processing_stages.length > 0 && (
                                <div className="flex flex-wrap gap-1 mb-2">
                                  {doc.processing_stages.map(stage => (
                                    <span key={stage} className="inline-flex items-center px-1.5 py-0.5 rounded text-xs bg-blue-50 text-blue-700">
                                      {stage}
                                    </span>
                                  ))}
                                </div>
                              )}

                              {/* Progress Bar for Processing */}
                              {doc.processing_status !== 'completed' && doc.processing_status !== 'failed' && (
                                <div className="w-full bg-gray-200 rounded-full h-1.5 mb-2">
                                  <div className="bg-yellow-500 h-1.5 rounded-full animate-pulse" style={{ width: '60%' }}></div>
                                </div>
                              )}

                              {/* Action Buttons */}
                              <div className="flex items-center gap-2">
                                {doc.processing_status === 'failed' && (
                                  <button
                                    onClick={() => setErrorModal(doc)}
                                    className="inline-flex items-center px-2 py-1 text-xs bg-red-50 text-red-700 rounded hover:bg-red-100 transition-colors"
                                  >
                                    <Eye className="h-3 w-3 mr-1" />
                                    View Error
                                  </button>
                                )}
                                {doc.processing_logs && doc.processing_logs.length > 0 && (
                                  <button
                                    onClick={() => setErrorModal(doc)}
                                    className="inline-flex items-center px-2 py-1 text-xs bg-blue-50 text-blue-700 rounded hover:bg-blue-100 transition-colors"
                                  >
                                    <FileText className="h-3 w-3 mr-1" />
                                    View Logs
                                  </button>
                                )}
                                {doc.processing_status === 'completed' && (
                                  <span className="inline-flex items-center px-2 py-1 text-xs text-green-700">
                                    {doc.chunks_count || 0} chunks • {doc.embeddings_count || 0} embeddings
                                  </span>
                                )}
                              </div>
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        );

      case 'analytics':
        return (
          <div className="w-full h-full flex flex-col">
            <div className="mb-6">
              <h2 className="text-2xl font-bold text-gray-800 mb-4">System Analytics</h2>
              <p className="text-gray-600 mb-6">
                Monitor system performance, service health, and processing metrics across your knowledge graph infrastructure.
              </p>

              {/* Service Health Overview */}
              {serviceStatus && (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4 mb-8">
                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Neo4j Database</p>
                        <p className={`text-lg font-semibold ${
                          serviceStatus.neo4j_connection ? 'text-green-600' : 'text-red-600'
                        }`}>
                          {serviceStatus.neo4j_connection ? 'Connected' : 'Disconnected'}
                        </p>
                      </div>
                      <div className={`p-2 rounded-full ${
                        serviceStatus.neo4j_connection ? 'bg-green-100' : 'bg-red-100'
                      }`}>
                        <div className={`w-3 h-3 rounded-full ${
                          serviceStatus.neo4j_connection ? 'bg-green-500' : 'bg-red-500'
                        }`} />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Tika Server</p>
                        <p className={`text-lg font-semibold ${
                          serviceStatus.tika_server ? 'text-green-600' : 'text-red-600'
                        }`}>
                          {serviceStatus.tika_server ? 'Running' : 'Stopped'}
                        </p>
                      </div>
                      <div className={`p-2 rounded-full ${
                        serviceStatus.tika_server ? 'bg-green-100' : 'bg-red-100'
                      }`}>
                        <div className={`w-3 h-3 rounded-full ${
                          serviceStatus.tika_server ? 'bg-green-500' : 'bg-red-500'
                        }`} />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">LLM Service</p>
                        <p className={`text-lg font-semibold ${
                          serviceStatus.llm_service ? 'text-green-600' : 'text-red-600'
                        }`}>
                          {serviceStatus.llm_service ? 'Running' : 'Stopped'}
                        </p>
                      </div>
                      <div className={`p-2 rounded-full ${
                        serviceStatus.llm_service ? 'bg-green-100' : 'bg-red-100'
                      }`}>
                        <div className={`w-3 h-3 rounded-full ${
                          serviceStatus.llm_service ? 'bg-green-500' : 'bg-red-500'
                        }`} />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Available Models</p>
                        <p className="text-lg font-semibold text-blue-600">
                          {serviceStatus.available_models?.length || 0}
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-blue-100">
                        <Settings className="w-3 h-3 text-blue-500" />
                      </div>
                    </div>
                  </div>

                  <div className="bg-white p-4 rounded-lg shadow-md">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="text-sm font-medium text-gray-600">System Uptime</p>
                        <p className="text-lg font-semibold text-green-600">
                          99.9%
                        </p>
                      </div>
                      <div className="p-2 rounded-full bg-green-100">
                        <div className="w-3 h-3 rounded-full bg-green-500" />
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Analytics Grid */}
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
                {/* Document Processing Analytics */}
                <div className="bg-white rounded-lg shadow-md p-6">
                  <h3 className="text-lg font-semibold text-gray-800 mb-4">Document Processing Analytics</h3>

                  <div className="mb-6">
                    <h4 className="text-md font-semibold text-gray-800 mb-4">Supported File Types</h4>
                    <div className="grid grid-cols-1 gap-3">
                      <div className="text-center p-3 bg-gray-50 rounded-lg">
                        <FileText className="h-6 w-6 mx-auto text-blue-500 mb-2" />
                        <p className="font-medium text-gray-700 text-sm">Documents</p>
                        <p className="text-xs text-gray-500">PDF, DOCX, TXT</p>
                      </div>
                      <div className="text-center p-3 bg-gray-50 rounded-lg">
                        <FileText className="h-6 w-6 mx-auto text-green-500 mb-2" />
                        <p className="font-medium text-gray-700 text-sm">Images</p>
                        <p className="text-xs text-gray-500">PNG, JPG, TIFF</p>
                      </div>
                      <div className="text-center p-3 bg-gray-50 rounded-lg">
                        <FileText className="h-6 w-6 mx-auto text-purple-500 mb-2" />
                        <p className="font-medium text-gray-700 text-sm">Spreadsheets</p>
                        <p className="text-xs text-gray-500">XLSX, CSV</p>
                      </div>
                    </div>
                  </div>

                  <div>
                    <h4 className="text-md font-semibold text-gray-800 mb-4">Processing Statistics</h4>
                    <div className="grid grid-cols-1 gap-3">
                      <div className="text-center p-3 bg-green-50 rounded-lg">
                        <div className="text-xl font-bold text-green-600">
                          {documents.filter(doc => doc.processing_status === 'completed').length}
                        </div>
                        <p className="text-xs text-green-700">Successfully Processed</p>
                      </div>
                      <div className="text-center p-3 bg-red-50 rounded-lg">
                        <div className="text-xl font-bold text-red-600">
                          {documents.filter(doc => doc.processing_status === 'failed').length}
                        </div>
                        <p className="text-xs text-red-700">Failed Processing</p>
                      </div>
                      <div className="text-center p-3 bg-blue-50 rounded-lg">
                        <div className="text-xl font-bold text-blue-600">
                          {documents.length}
                        </div>
                        <p className="text-xs text-blue-700">Total Documents</p>
                      </div>
                    </div>
                  </div>
                </div>

                {/* System Performance Metrics */}
                <div className="bg-white rounded-lg shadow-md p-6">
                  <h3 className="text-lg font-semibold text-gray-800 mb-4">System Performance Metrics</h3>

                  <div className="space-y-4">
                    <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Average Processing Time</p>
                        <p className="text-lg font-semibold text-blue-600">2.3 min</p>
                      </div>
                      <div className="p-2 rounded-full bg-blue-100">
                        <Settings className="w-4 h-4 text-blue-500" />
                      </div>
                    </div>

                    <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Memory Usage</p>
                        <p className="text-lg font-semibold text-purple-600">68%</p>
                      </div>
                      <div className="p-2 rounded-full bg-purple-100">
                        <BarChart3 className="w-4 h-4 text-purple-500" />
                      </div>
                    </div>

                    <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                      <div>
                        <p className="text-sm font-medium text-gray-600">CPU Utilization</p>
                        <p className="text-lg font-semibold text-orange-600">45%</p>
                      </div>
                      <div className="p-2 rounded-full bg-orange-100">
                        <BarChart3 className="w-4 h-4 text-orange-500" />
                      </div>
                    </div>

                    <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Active Connections</p>
                        <p className="text-lg font-semibold text-green-600">12</p>
                      </div>
                      <div className="p-2 rounded-full bg-green-100">
                        <Network className="w-4 h-4 text-green-500" />
                      </div>
                    </div>

                    <div className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                      <div>
                        <p className="text-sm font-medium text-gray-600">Storage Used</p>
                        <p className="text-lg font-semibold text-indigo-600">2.4 GB</p>
                      </div>
                      <div className="p-2 rounded-full bg-indigo-100">
                        <FileText className="w-4 h-4 text-indigo-500" />
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/* Recent Activity */}
              <div className="bg-white rounded-lg shadow-md p-6">
                <h3 className="text-lg font-semibold text-gray-800 mb-4">Recent Activity</h3>
                <div className="space-y-3">
                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <div className="p-2 rounded-full bg-green-100">
                        <FileText className="w-4 h-4 text-green-500" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">Document processed successfully</p>
                        <p className="text-xs text-gray-500">annual_report.pdf - 2 minutes ago</p>
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <div className="p-2 rounded-full bg-blue-100">
                        <Network className="w-4 h-4 text-blue-500" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">Graph nodes updated</p>
                        <p className="text-xs text-gray-500">Added 15 new relationships - 5 minutes ago</p>
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <div className="p-2 rounded-full bg-red-100">
                        <AlertCircle className="w-4 h-4 text-red-500" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">Processing failed</p>
                        <p className="text-xs text-gray-500">corrupted_file.docx - 10 minutes ago</p>
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <div className="p-2 rounded-full bg-purple-100">
                        <MessageSquare className="w-4 h-4 text-purple-500" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">AI query processed</p>
                        <p className="text-xs text-gray-500">Search completed in 1.2 seconds - 15 minutes ago</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="h-screen flex bg-gray-100">
      {/* Main Content Area */}
      <div className="flex-1 flex flex-col">
        {/* Header with Tabs */}
        <div className="bg-white shadow-sm border-b">
          <div className="px-6 py-4">
            <div className="flex items-center justify-between">
              <h1 className="text-2xl font-bold text-gray-900">NeoBoi Dashboard</h1>
              <div className="flex items-center space-x-4">
                <button
                  onClick={fetchGraphData}
                  className="px-3 py-1 text-sm bg-blue-100 text-blue-700 rounded-md hover:bg-blue-200 transition-colors"
                >
                  Refresh Graph
                </button>
                <button
                  onClick={checkServiceStatus}
                  className="px-3 py-1 text-sm bg-gray-100 text-gray-700 rounded-md hover:bg-gray-200 transition-colors"
                >
                  Refresh Status
                </button>
              </div>
            </div>

            {/* Tab Navigation */}
            <div className="mt-4">
              <nav className="flex space-x-8">
                {tabs.map((tab) => {
                  const Icon = tab.icon;
                  return (
                    <button
                      key={tab.id}
                      onClick={() => setActiveTab(tab.id)}
                      className={`flex items-center px-1 py-2 border-b-2 font-medium text-sm transition-colors ${
                        activeTab === tab.id
                          ? 'border-blue-500 text-blue-600'
                          : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                      }`}
                    >
                      <Icon className="h-4 w-4 mr-2" />
                      {tab.label}
                    </button>
                  );
                })}
              </nav>
            </div>
          </div>
        </div>

        {/* Main Content */}
        <div className="flex-1 p-6 overflow-hidden">
          <div className="bg-white rounded-lg shadow-sm h-full overflow-hidden">
            {renderTabContent()}
          </div>
        </div>
      </div>

      {/* Collapsible Chat Panel */}
      <div className={`bg-white border-l border-gray-200 transition-all duration-300 ${
        chatCollapsed ? 'w-12' : 'w-96'
      }`}>
        <div className="h-full flex flex-col">
          {/* Chat Header */}
          <div className="p-3 border-b border-gray-200 flex items-center justify-between bg-gray-50">
            {!chatCollapsed && (
              <h3 className="font-semibold text-gray-800 flex items-center gap-2">
                <MessageSquare className="h-4 w-4" />
                AI Assistant
              </h3>
            )}
            <button
              onClick={() => setChatCollapsed(!chatCollapsed)}
              className="p-1 hover:bg-gray-200 rounded transition-colors"
            >
              {chatCollapsed ? (
                <ChevronUp className="h-4 w-4 text-gray-600" />
              ) : (
                <ChevronDown className="h-4 w-4 text-gray-600" />
              )}
            </button>
          </div>

          {/* Chat Content */}
          {!chatCollapsed && (
            <div className="flex-1 overflow-hidden">
              <ChatInterface
                onGraphUpdate={handleGraphUpdate}
                onSearchResult={handleSearchResults}
                loading={loading}
              />
            </div>
          )}
        </div>
      </div>

      {/* Error Modal */}
      {errorModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full mx-4 max-h-[80vh] overflow-hidden">
            <div className="flex items-center justify-between p-6 border-b border-gray-200">
              <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
                <AlertCircle className="h-5 w-5 text-red-500" />
                Processing Error Details
              </h3>
              <button
                onClick={() => setErrorModal(null)}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <X className="h-6 w-6" />
              </button>
            </div>

            <div className="p-6 overflow-y-auto max-h-[60vh]">
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Document Information</h4>
                  <div className="bg-gray-50 p-3 rounded-md">
                    <p className="text-sm"><strong>Filename:</strong> {errorModal.filename}</p>
                    <p className="text-sm"><strong>Type:</strong> {errorModal.processing_type}</p>
                    <p className="text-sm"><strong>Size:</strong> {errorModal.file_size ? `${(errorModal.file_size / 1024).toFixed(1)} KB` : 'N/A'}</p>
                    <p className="text-sm"><strong>Processed At:</strong> {errorModal.processed_at ? new Date(errorModal.processed_at).toLocaleString() : 'N/A'}</p>
                  </div>
                </div>

                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Error Message</h4>
                  <div className="bg-red-50 border border-red-200 p-3 rounded-md">
                    <p className="text-sm text-red-800 whitespace-pre-wrap">
                      {errorModal.error_message || 'No error message available'}
                    </p>
                  </div>
                </div>

                {errorModal.error_details && (
                  <div>
                    <h4 className="font-medium text-gray-900 mb-2">Additional Error Details</h4>
                    <div className="bg-red-50 border border-red-200 p-3 rounded-md">
                      <pre className="text-xs text-red-800 whitespace-pre-wrap overflow-x-auto">
                        {typeof errorModal.error_details === 'string'
                          ? errorModal.error_details
                          : JSON.stringify(errorModal.error_details, null, 2)
                        }
                      </pre>
                    </div>
                  </div>
                )}

                {errorModal.processing_logs && errorModal.processing_logs.length > 0 && (
                  <div>
                    <h4 className="font-medium text-gray-900 mb-2">Processing Logs</h4>
                    <div className="bg-gray-50 border p-3 rounded-md max-h-40 overflow-y-auto">
                      <div className="space-y-1">
                        {errorModal.processing_logs.map((log, index) => (
                          <div key={index} className="text-xs text-gray-700 font-mono">
                            {log}
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                )}

                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Processing Stages Completed</h4>
                  <div className="flex flex-wrap gap-2">
                    {errorModal.processing_stages && errorModal.processing_stages.length > 0 ? (
                      errorModal.processing_stages.map(stage => (
                        <span key={stage} className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          {stage}
                        </span>
                      ))
                    ) : (
                      <span className="text-sm text-gray-500">No stages completed</span>
                    )}
                  </div>
                </div>
              </div>
            </div>

            <div className="flex justify-end gap-3 p-6 border-t border-gray-200">
              <button
                onClick={() => setErrorModal(null)}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 border border-gray-300 rounded-md hover:bg-gray-200 transition-colors"
              >
                Close
              </button>
              <button
                onClick={() => {
                  // Could implement retry functionality here
                  setErrorModal(null);
                  // fetchDocuments(); // Refresh to see if document was re-processed
                }}
                className="px-4 py-2 text-sm font-medium text-white bg-blue-600 border border-transparent rounded-md hover:bg-blue-700 transition-colors"
              >
                Retry Processing
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UnifiedDashboard;