import { useRef, useEffect, useState, useCallback } from 'react';
import cytoscape from 'cytoscape';
import coseBilkent from 'cytoscape-cose-bilkent';
import popper from 'cytoscape-popper';
import tippy from 'tippy.js';
import 'tippy.js/dist/tippy.css';

// Register Cytoscape extensions
cytoscape.use(coseBilkent);
cytoscape.use(popper);

// --- Helper Functions ---

/**
 * Transforms raw graph data from the API into the format required by Cytoscape.js.
 * It classifies nodes and edges to apply specific styles.
 * @param {object} graphData - The raw graph data from the backend.
 * @returns {Array} - An array of elements formatted for Cytoscape.
 */
function transformDataForCytoscape(graphData) {
    const elements = [];
    if (!graphData) {
        console.warn('[Transform] No graph data provided');
        return elements;
    }

    console.log('[Transform] Processing graph data:', {
        hasNodes: !!graphData.nodes,
        nodeCount: graphData.nodes?.length || 0,
        hasEdges: !!graphData.edges,
        edgeCount: graphData.edges?.length || 0
    });

    // 1. Process Nodes
    if (graphData.nodes && Array.isArray(graphData.nodes)) {
        graphData.nodes.forEach((node, index) => {
            try {
                // Ensure node has required properties
                if (!node || typeof node !== 'object') {
                    console.warn(`[Transform] Invalid node at index ${index}:`, node);
                    return;
                }

                const nodeId = node.id || node.elementId || `node_${index}`;
                if (!nodeId) {
                    console.warn(`[Transform] Node missing ID at index ${index}:`, node);
                    return;
                }

                const properties = node.properties || {};
                const name = node.label || properties.name || properties.title || `Node ${nodeId}`;
                const neo4jLabels = node.group ? [node.group] : (properties.labels || ['Unknown']);

                // Enhanced node type classification
                let nodeType = 'unknown';
                const typeKeywords = {
                    supplier: ['supplier', 'vendor'],
                    manufacturer: ['manufacturer', 'factory', 'producer'],
                    customer: ['customer', 'client', 'buyer'],
                    distributor: ['distributor', 'dealer'],
                    retailer: ['retailer'],
                    warehouse: ['warehouse', 'storage'],
                    logistics: ['logistics', 'shipping'],
                };

                const checkKeywords = (text, keywords) => keywords.some(kw => String(text).toLowerCase().includes(kw));

                for (const [type, keywords] of Object.entries(typeKeywords)) {
                    if (checkKeywords(neo4jLabels.join(' ').toLowerCase(), keywords) || checkKeywords(name.toLowerCase(), keywords)) {
                        nodeType = type;
                        break;
                    }
                }

                // Status classification for styling
                const status = String(properties.status || '').toLowerCase();
                let statusClass = '';
                if (status.includes('active') || status.includes('online')) statusClass = 'active';
                else if (status.includes('inactive') || status.includes('offline')) statusClass = 'inactive';
                else if (status.includes('critical') || status.includes('error')) statusClass = 'critical';

                elements.push({
                    group: 'nodes',
                    data: {
                        id: String(nodeId),
                        label: String(name),
                        neo4j_labels: neo4jLabels,
                        group: node.group || nodeType,
                        properties: properties,
                        nodeType: nodeType,
                    },
                    classes: `${nodeType} ${statusClass}`.trim(),
                });
            } catch (error) {
                console.error(`[Transform] Error processing node at index ${index}:`, error, node);
            }
        });
    } else {
        console.warn('[Transform] graphData.nodes is not an array or is missing');
    }

    // 2. Process Edges
    if (graphData.edges && Array.isArray(graphData.edges)) {
        graphData.edges.forEach((edge, index) => {
            try {
                // Ensure edge has required properties
                if (!edge || typeof edge !== 'object') {
                    console.warn(`[Transform] Invalid edge at index ${index}:`, edge);
                    return;
                }

                const edgeId = edge.id || edge.elementId || `edge_${index}`;
                if (!edgeId) {
                    console.warn(`[Transform] Edge missing ID at index ${index}:`, edge);
                    return;
                }

                // Handle different edge source/target formats
                const sourceId = edge.from || edge.source || edge.startNodeElementId || edge.start;
                const targetId = edge.to || edge.target || edge.endNodeElementId || edge.end;

                if (!sourceId || !targetId) {
                    console.warn(`[Transform] Edge missing source/target at index ${index}:`, edge);
                    return;
                }

                const properties = edge.properties || {};
                const label = edge.label || properties.type || properties.relationship || 'RELATED_TO';

                // Edge type classification
                let edgeType = 'related';
                const labelLower = String(label).toLowerCase();
                if (labelLower.includes('supplies')) edgeType = 'supplies';
                else if (labelLower.includes('manufactures')) edgeType = 'manufactures';
                else if (labelLower.includes('distributes')) edgeType = 'distributes';
                else if (labelLower.includes('sells')) edgeType = 'sells';

                const isCritical = properties.status === 'CRITICAL' || properties.priority === 'HIGH';

                elements.push({
                    group: 'edges',
                    data: {
                        id: String(edgeId),
                        source: String(sourceId),
                        target: String(targetId),
                        label: String(label),
                        properties: properties,
                        edgeType: edgeType,
                    },
                    classes: `${edgeType} ${isCritical ? 'critical' : ''}`.trim(),
                });
            } catch (error) {
                console.error(`[Transform] Error processing edge at index ${index}:`, error, edge);
            }
        });
    } else {
        console.warn('[Transform] graphData.edges is not an array or is missing');
    }

    console.log(`[Transform] Successfully converted ${elements.length} elements for Cytoscape (${elements.filter(e => e.group === 'nodes').length} nodes, ${elements.filter(e => e.group === 'edges').length} edges)`);
    return elements;
}


// --- Stylesheet ---

const cytoscapeStylesheet = [
    // Base node styling
    {
        selector: 'node',
        style: {
            'background-color': '#95A5A6', 'border-color': '#7F8C8D', 'border-width': 2,
            'label': 'data(label)', 'width': 65, 'height': 65, 'font-size': '11px',
            'font-family': '"Segoe UI", "Helvetica Neue", Arial, sans-serif', 'font-weight': '600',
            'text-valign': 'center', 'color': '#2C3E50', 'text-wrap': 'wrap', 'text-max-width': '55px',
            'transition-property': 'border-width, background-color', 'transition-duration': '0.2s'
        }
    },
    // Node type styles
    { selector: 'node.supplier', style: { 'background-color': '#27AE60', 'border-color': '#229954', 'shape': 'ellipse' } },
    { selector: 'node.manufacturer', style: { 'background-color': '#3498DB', 'border-color': '#2980B9', 'shape': 'round-rectangle' } },
    { selector: 'node.customer', style: { 'background-color': '#E74C3C', 'border-color': '#C0392B', 'shape': 'diamond' } },
    { selector: 'node.distributor', style: { 'background-color': '#F39C12', 'border-color': '#E67E22', 'shape': 'hexagon' } },
    { selector: 'node.retailer', style: { 'background-color': '#9B59B6', 'border-color': '#8E44AD', 'shape': 'star' } },
    { selector: 'node.warehouse', style: { 'background-color': '#16A085', 'border-color': '#138D75', 'shape': 'round-triangle' } },
    { selector: 'node.logistics', style: { 'background-color': '#D35400', 'border-color': '#BA4A00', 'shape': 'vee' } },
    // Status styles
    { selector: 'node.active', style: { 'border-width': 4, 'border-color': '#2ECC71' } },
    { selector: 'node.inactive', style: { 'opacity': 0.7, 'border-style': 'dashed' } },
    { selector: 'node.critical', style: { 'border-color': '#E74C3C', 'border-width': 5, 'border-style': 'double' } },
    // Base edge styling
    {
        selector: 'edge',
        style: {
            'width': 3, 'line-color': '#BDC3C7', 'target-arrow-color': '#BDC3C7', 'target-arrow-shape': 'triangle',
            'curve-style': 'bezier', 'font-size': '9px', 'font-family': '"Segoe UI", Arial, sans-serif',
            'color': '#34495E', 'text-background-opacity': 1, 'text-background-color': '#FFFFFF',
            'text-background-padding': '3px', 'text-border-color': '#BDC3C7', 'text-border-width': 1,
            'transition-property': 'width, line-color, target-arrow-color', 'transition-duration': '0.3s'
        }
    },
    // Edge type styles
    { selector: 'edge.supplies', style: { 'line-color': '#27AE60', 'target-arrow-color': '#27AE60', 'width': 4 } },
    { selector: 'edge.manufactures', style: { 'line-color': '#3498DB', 'target-arrow-color': '#3498DB', 'width': 4 } },
    { selector: 'edge.distributes', style: { 'line-color': '#F39C12', 'target-arrow-color': '#F39C12', 'line-style': 'dashed', 'width': 4 } },
    { selector: 'edge.sells', style: { 'line-color': '#9B59B6', 'target-arrow-color': '#9B59B6', 'width': 4 } },
    { selector: 'edge.critical', style: { 'width': 6, 'line-color': '#E74C3C', 'target-arrow-color': '#E74C3C' } },
    // Interaction styles
    { selector: 'node:selected', style: { 'border-width': 5, 'border-color': '#2C3E50', 'border-style': 'double' } },
    { selector: 'edge:selected', style: { 'width': 6, 'line-color': '#2C3E50', 'target-arrow-color': '#2C3E50' } },
    // Highlighting styles
    { selector: 'edge.connected', style: { 'width': 5, 'line-color': '#F39C12', 'target-arrow-color': '#F39C12', 'z-index': 10 } },
    { selector: 'node.neighbor', style: { 'border-width': 3, 'border-color': '#F39C12' } },
    { selector: '.dimmed', style: { 'opacity': 0.3 } }
];


// --- React Components ---

const Legend = ({ nodeTypes = [], edgeTypes = [] }) => {
    // Define color and shape mappings for node types
    const nodeTypeStyles = {
        supplier: { background: '#27AE60', shape: 'circle' },
        manufacturer: { background: '#3498DB', shape: 'round-rectangle' },
        customer: { background: '#E74C3C', shape: 'diamond' },
        distributor: { background: '#F39C12', shape: 'hexagon' },
        retailer: { background: '#9B59B6', shape: 'star' },
        warehouse: { background: '#16A085', shape: 'round-triangle' },
        logistics: { background: '#D35400', shape: 'vee' },
        unknown: { background: '#95A5A6', shape: 'circle' }
    };

    // Define color mappings for edge types
    const edgeTypeStyles = {
        supplies: { background: '#27AE60', style: 'solid' },
        manufactures: { background: '#3498DB', style: 'solid' },
        distributes: { background: '#F39C12', style: 'dashed' },
        sells: { background: '#9B59B6', style: 'solid' },
        related: { background: '#BDC3C7', style: 'solid' }
    };

    const getNodeShapeStyle = (shape) => {
        switch (shape) {
            case 'circle': return { borderRadius: '50%' };
            case 'round-rectangle': return { borderRadius: '4px' };
            case 'diamond': return { transform: 'rotate(45deg)' };
            case 'hexagon': return { clipPath: 'polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%)' };
            case 'star': return { clipPath: 'polygon(50% 0%, 93.3% 25%, 93.3% 75%, 50% 100%, 6.7% 75%, 6.7% 25%)' };
            case 'round-triangle': return { clipPath: 'polygon(50% 0%, 0% 100%, 100% 100%)' };
            case 'vee': return { clipPath: 'polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%)' };
            default: return { borderRadius: '50%' };
        }
    };

    const getEdgeStyle = (style) => {
        if (style === 'dashed') {
            return { borderStyle: 'dashed', borderWidth: '1px 0', borderColor: 'currentColor' };
        }
        return {};
    };

    return (
        <div style={{
            position: 'absolute', top: '10px', right: '10px', background: 'rgba(255, 255, 255, 0.95)',
            border: '1px solid #BDC3C7', borderRadius: '6px', padding: '8px', fontSize: '11px',
            fontFamily: '"Segoe UI", Arial, sans-serif', boxShadow: '0 2px 8px rgba(0,0,0,0.1)', zIndex: 100,
            maxHeight: '70vh', overflowY: 'auto'
        }}>
            <div style={{ fontWeight: '600', marginBottom: '6px', color: '#2C3E50' }}>Legend</div>

            {/* Node Types */}
            {nodeTypes.length > 0 && (
                <>
                    <div style={{ fontWeight: '600', marginTop: '8px', marginBottom: '4px', fontSize: '10px', color: '#34495E' }}>Node Types</div>
                    {nodeTypes.map(nodeType => {
                        const style = nodeTypeStyles[nodeType] || nodeTypeStyles.unknown;
                        return (
                            <div key={nodeType} style={{ display: 'flex', alignItems: 'center', gap: '4px', marginBottom: '2px' }}>
                                <div style={{
                                    width: '12px', height: '12px', background: style.background,
                                    ...getNodeShapeStyle(style.shape)
                                }}></div>
                                <span style={{ textTransform: 'capitalize' }}>{nodeType}</span>
                            </div>
                        );
                    })}
                </>
            )}

            {/* Edge Types */}
            {edgeTypes.length > 0 && (
                <>
                    <div style={{ fontWeight: '600', marginTop: '8px', marginBottom: '4px', fontSize: '10px', color: '#34495E' }}>Relationships</div>
                    {edgeTypes.map(edgeType => {
                        const style = edgeTypeStyles[edgeType] || edgeTypeStyles.related;
                        return (
                            <div key={edgeType} style={{ display: 'flex', alignItems: 'center', gap: '4px', marginBottom: '2px' }}>
                                <div style={{
                                    width: '20px', height: '2px', background: style.background,
                                    ...getEdgeStyle(style.style)
                                }}></div>
                                <span style={{ textTransform: 'capitalize' }}>{edgeType}</span>
                            </div>
                        );
                    })}
                </>
            )}

            {/* Keyboard Shortcuts */}
            <div style={{ fontWeight: '600', marginTop: '8px', marginBottom: '4px', fontSize: '10px', color: '#34495E' }}>Shortcuts</div>
            <div style={{ fontSize: '9px', color: '#7F8C8D', lineHeight: '1.3' }}>
                F: Fit, C: Center, L: Toggle Legend, R: Reset, Esc: Clear
            </div>
        </div>
    );
};

const LoadingIndicator = () => (
    <div style={{
        position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 10,
        background: 'rgba(255, 255, 255, 0.9)', padding: '20px', borderRadius: '8px', textAlign: 'center'
    }}>
        <div style={{ fontSize: '16px', fontWeight: '600', color: '#2C3E50' }}>Loading Graph...</div>
    </div>
);

const EmptyGraphMessage = () => (
    <div style={{
        position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 10,
        background: 'rgba(255, 255, 255, 0.95)', padding: '30px', borderRadius: '12px', textAlign: 'center',
        border: '2px solid #BDC3C7'
    }}>
        <div style={{ fontSize: '48px', marginBottom: '16px', color: '#BDC3C7' }}>📊</div>
        <div style={{ fontSize: '18px', fontWeight: '600', color: '#2C3E50', marginBottom: '12px' }}>No Graph Data</div>
        <div style={{ fontSize: '14px', color: '#7F8C8D' }}>Upload documents or use the chat to build your knowledge graph.</div>
    </div>
);


// --- Main Component ---

const GraphVisualization = ({ graphData, onNodeSelect, loading }) => {
    const containerRef = useRef(null);
    const cyRef = useRef(null);
    const [showLegend, setShowLegend] = useState(true);
    const [nodeTypes, setNodeTypes] = useState([]);
    const [edgeTypes, setEdgeTypes] = useState([]);

    const highlightRelationships = useCallback((element) => {
        const cy = cyRef.current;
        if (!cy) return;
        cy.elements().removeClass('connected neighbor dimmed');
        if (element && element.isNode()) {
            element.connectedEdges().addClass('connected');
            element.neighborhood('node').addClass('neighbor');
            cy.elements().not(element.neighborhood()).not(element).addClass('dimmed');
        }
    }, []);

    // Initialize Cytoscape
    useEffect(() => {
        if (!containerRef.current) {
            console.warn('[Cytoscape] Container not ready');
            return;
        }

        console.log('[Cytoscape] Initializing Cytoscape instance');

        let cy;
        try {
            cy = cytoscape({
                container: containerRef.current,
                style: cytoscapeStylesheet,
                layout: { name: 'cose-bilkent', animate: 'end', animationDuration: 1000, fit: true, padding: 50 },
                minZoom: 0.1,
                maxZoom: 3,
                pixelRatio: 'auto'
            });
            cyRef.current = cy;
            console.log('[Cytoscape] Cytoscape instance created successfully');
        } catch (error) {
            console.error('[Cytoscape] Failed to initialize Cytoscape:', error);
            return;
        }

        // --- Event Handlers ---
        let tippyInstance;

        const makeTippy = (target, text) => {
            try {
                if (!target || !target.popperRef) {
                    console.warn('[Cytoscape] Invalid target for tooltip:', target);
                    return null;
                }

                // Create a proper DOM element for tippy
                const dummyEl = document.createElement('div');
                dummyEl.style.position = 'absolute';
                dummyEl.style.visibility = 'hidden';
                dummyEl.style.pointerEvents = 'none';
                document.body.appendChild(dummyEl);

                const tippyInstance = tippy(dummyEl, {
                    content: text,
                    trigger: 'manual',
                    allowHTML: true,
                    arrow: true,
                    placement: 'bottom',
                    hideOnClick: false,
                    interactive: true,
                    theme: 'light-border',
                    maxWidth: '300px',
                    // Position the tooltip using Cytoscape's popper ref
                    getReferenceClientRect: () => {
                        try {
                            const ref = target.popperRef();
                            return ref ? ref.getBoundingClientRect() : new DOMRect();
                        } catch (error) {
                            console.warn('[Cytoscape] Error getting popper ref:', error);
                            return new DOMRect();
                        }
                    },
                    onHidden: () => {
                        // Clean up the dummy element when tooltip is hidden
                        if (dummyEl && dummyEl.parentNode) {
                            dummyEl.parentNode.removeChild(dummyEl);
                        }
                    }
                });

                return tippyInstance;
            } catch (error) {
                console.error('[Cytoscape] Failed to create tooltip:', error);
                return null;
            }
        };

        cy.on('mouseover', 'node, edge', (e) => {
            try {
                const el = e.target;

                // Check if element is still valid
                if (!el || !el.data || typeof el.data !== 'function') {
                    console.warn('[Cytoscape] Invalid element in mouseover:', el);
                    return;
                }

                const props = el.data('properties') || {};
                let content = `<strong>${el.data('label') || 'Unknown'}</strong><hr/>`;

                // Limit properties shown in tooltip
                const propEntries = Object.entries(props);
                const maxProps = 10;
                for (let i = 0; i < Math.min(propEntries.length, maxProps); i++) {
                    const [key, value] = propEntries[i];
                    const displayValue = typeof value === 'object' ? JSON.stringify(value).substring(0, 50) + '...' : String(value);
                    content += `<strong>${key}:</strong> ${displayValue}<br/>`;
                }

                if (propEntries.length > maxProps) {
                    content += `<em>... and ${propEntries.length - maxProps} more properties</em>`;
                }

                // Destroy any existing tooltip first
                if (tippyInstance) {
                    try {
                        tippyInstance.destroy();
                    } catch (destroyError) {
                        console.warn('[Cytoscape] Error destroying existing tooltip:', destroyError);
                    }
                    tippyInstance = null;
                }

                tippyInstance = makeTippy(el, content);
                if (tippyInstance) {
                    tippyInstance.show();
                }
                highlightRelationships(el);
            } catch (error) {
                console.error('[Cytoscape] Error in mouseover handler:', error);
            }
        });

        cy.on('mouseout', 'node, edge', () => {
            try {
                if (tippyInstance) {
                    try {
                        tippyInstance.destroy();
                    } catch (destroyError) {
                        console.warn('[Cytoscape] Error destroying tooltip in mouseout:', destroyError);
                    }
                    tippyInstance = null;
                }
                highlightRelationships(null);
            } catch (error) {
                console.error('[Cytoscape] Error in mouseout handler:', error);
            }
        });

        cy.on('tap', 'node', (e) => {
            try {
                if (onNodeSelect) {
                    onNodeSelect(e.target.data());
                }
            } catch (error) {
                console.error('[Cytoscape] Error in node tap handler:', error);
            }
        });

        cy.on('tap', (e) => {
            try {
                if (e.target === cy) {
                    cy.elements().unselect();
                    highlightRelationships(null);
                }
            } catch (error) {
                console.error('[Cytoscape] Error in background tap handler:', error);
            }
        });

        // Keyboard shortcuts
        const handleKeyPress = (e) => {
            try {
                if (!cyRef.current) return;
                const cy = cyRef.current;

                switch (e.key.toLowerCase()) {
                    case 'f':
                        cy.fit(null, 50);
                        console.log('[Cytoscape] Fit to screen');
                        break;
                    case 'c':
                        cy.center(cy.$(':selected'));
                        console.log('[Cytoscape] Center on selected');
                        break;
                    case 'l':
                        setShowLegend(prev => !prev);
                        console.log('[Cytoscape] Toggle legend');
                        break;
                    case 'r':
                        try {
                            cy.layout({ name: 'cose-bilkent', animate: true }).run();
                            console.log('[Cytoscape] Reset layout');
                        } catch (layoutError) {
                            console.error('[Cytoscape] Layout error:', layoutError);
                            // Fallback to simpler layout
                            cy.layout({ name: 'grid', animate: true }).run();
                        }
                        break;
                    case 'escape':
                        cy.elements().unselect();
                        highlightRelationships(null);
                        console.log('[Cytoscape] Clear selection');
                        break;
                }
            } catch (error) {
                console.error('[Cytoscape] Error in keypress handler:', error);
            }
        };

        document.addEventListener('keydown', handleKeyPress);

        return () => {
            console.log('[Cytoscape] Cleaning up Cytoscape instance');
            document.removeEventListener('keydown', handleKeyPress);
            try {
                if (cy) {
                    cy.destroy();
                }
            } catch (error) {
                console.error('[Cytoscape] Error destroying Cytoscape instance:', error);
            }
        };
    }, [onNodeSelect, highlightRelationships]);

    // Update graph data when prop changes
    useEffect(() => {
        const cy = cyRef.current;
        if (!cy) {
            console.warn('[Cytoscape] Cytoscape instance not ready for data update');
            return;
        }

        if (!graphData) {
            console.log('[Cytoscape] No graph data to update');
            setNodeTypes([]);
            setEdgeTypes([]);
            return;
        }

        console.log('[Cytoscape] Updating graph data');

        try {
            const elements = transformDataForCytoscape(graphData);

            if (elements.length === 0) {
                console.warn('[Cytoscape] No valid elements to display');
                cy.elements().remove();
                setNodeTypes([]);
                setEdgeTypes([]);
                return;
            }

            // Extract unique node and edge types for dynamic legend
            const uniqueNodeTypes = new Set();
            const uniqueEdgeTypes = new Set();

            elements.forEach(element => {
                if (element.group === 'nodes') {
                    const nodeType = element.data.nodeType || 'unknown';
                    uniqueNodeTypes.add(nodeType);
                } else if (element.group === 'edges') {
                    const edgeType = element.data.edgeType || 'related';
                    uniqueEdgeTypes.add(edgeType);
                }
            });

            setNodeTypes(Array.from(uniqueNodeTypes).sort());
            setEdgeTypes(Array.from(uniqueEdgeTypes).sort());

            // Remove existing elements
            cy.elements().remove();

            // Add new elements
            cy.add(elements);

            const nodeCount = cy.nodes().length;
            const edgeCount = cy.edges().length;

            console.log(`[Cytoscape] Added ${nodeCount} nodes and ${edgeCount} edges`);
            console.log(`[Cytoscape] Found node types: ${Array.from(uniqueNodeTypes).join(', ')}`);
            console.log(`[Cytoscape] Found edge types: ${Array.from(uniqueEdgeTypes).join(', ')}`);

            // Only run layout if there are elements to layout
            if (nodeCount > 0) {
                try {
                    // Intelligent layout selection based on graph characteristics
                    let layoutConfig;

                    if (edgeCount === 0) {
                        // For nodes-only graphs (like the current FoodNutrient data)
                        console.log('[Cytoscape] Using grid layout for nodes-only graph');
                        layoutConfig = {
                            name: 'grid',
                            animate: true,
                            fit: true,
                            padding: 30,
                            avoidOverlap: true,
                            avoidOverlapPadding: 10
                        };
                    } else if (nodeCount > 50) {
                        // For large graphs, use simpler layout
                        console.log('[Cytoscape] Using cose layout for large graph');
                        layoutConfig = {
                            name: 'cose',
                            animate: 'end',
                            animationDuration: 1000,
                            fit: true,
                            padding: 50,
                            nodeRepulsion: 4000,
                            idealEdgeLength: 100,
                            edgeElasticity: 0.45,
                            gravity: 0.25,
                            numIter: 1000
                        };
                    } else {
                        // For smaller connected graphs, use COSE-Bilkent with safer parameters
                        console.log('[Cytoscape] Using cose-bilkent layout for connected graph');
                        layoutConfig = {
                            name: 'cose-bilkent',
                            animate: 'end',
                            animationDuration: 1000,
                            fit: true,
                            padding: 50,
                            nodeRepulsion: 4500,
                            idealEdgeLength: 150,
                            edgeElasticity: 0.45,
                            nestingFactor: 0.1,
                            gravity: 0.25,
                            numIter: 1000, // Reduced from 2500
                            tile: false, // Disabled tiling for stability
                            randomize: true // Add randomization for better convergence
                        };
                    }

                    const layout = cy.layout(layoutConfig);
                    layout.run();
                } catch (layoutError) {
                    console.error('[Cytoscape] Primary layout failed, trying fallback:', layoutError);

                    // Fallback to simplest possible layout
                    try {
                        cy.layout({
                            name: 'random',
                            animate: false,
                            fit: true,
                            padding: 30
                        }).run();
                        console.log('[Cytoscape] Fallback random layout applied');
                    } catch (fallbackError) {
                        console.error('[Cytoscape] All layouts failed:', fallbackError);
                        // Last resort: just fit without layout
                        try {
                            cy.fit(null, 50);
                            console.log('[Cytoscape] Applied fit-only as last resort');
                        } catch (fitError) {
                            console.error('[Cytoscape] Even fit failed:', fitError);
                        }
                    }
                }
            } else {
                console.log('[Cytoscape] No nodes to layout');
            }

        } catch (error) {
            console.error('[Cytoscape] Error updating graph data:', error);
            // Try to clear the graph on error
            try {
                cy.elements().remove();
                setNodeTypes([]);
                setEdgeTypes([]);
            } catch (clearError) {
                console.error('[Cytoscape] Error clearing graph on failure:', clearError);
            }
        }

    }, [graphData]);

    const isGraphEmpty = !graphData || (!graphData.nodes?.length && !graphData.edges?.length);

    return (
        <div style={{ width: '100%', height: '100%', position: 'relative', background: '#FFFFFF', border: '1px solid #BDC3C7', borderRadius: '8px' }}>
            {showLegend && <Legend nodeTypes={nodeTypes} edgeTypes={edgeTypes} />}
            {loading && <LoadingIndicator />}
            {!loading && isGraphEmpty && <EmptyGraphMessage />}
            <div ref={containerRef} style={{ width: '100%', height: '100%', borderRadius: '8px' }} />
        </div>
    );
};

export default GraphVisualization;